$(document).ready(function() {
    const $container = $('.main-genre-ranking > .slider-container-rank > .slider-wrapper');
    const $btnLeft = $('.main-genre-ranking > .slider-container-rank > .nav-button.left');
    const $btnRight = $('.main-genre-ranking > .slider-container-rank > .nav-button.right');
    const $dotsContainer = $('.dots-container');
    let currentIndex = 0;
    const itemsToShow = 5;
    const gap = parseInt($('.performance-list-genre').css('gap')) || 0;

    const updateSliderPosition = () => {
        const $items = $container.find('.performance-info'); // Ïä¨ÎùºÏù¥Îçî ÏïÑÏù¥ÌÖú Ïû¨Ï¥àÍ∏∞Ìôî
        const itemWidth = $items.first().outerWidth() + gap;
        $container.css('transform', `translateX(-${currentIndex * itemWidth}px)`);
        updateButtonStates();
        updateDots();
    };

    const updateButtonStates = () => {
        const $items = $container.find('.performance-info'); // Ïä¨ÎùºÏù¥Îçî ÏïÑÏù¥ÌÖú Ïû¨Ï¥àÍ∏∞Ìôî
        $btnLeft.prop('disabled', currentIndex <= 0);
        $btnRight.prop('disabled', currentIndex >= $items.length - itemsToShow);
    };

    const createDots = () => {
        $dotsContainer.empty(); // Í∏∞Ï°¥ dot Ï†úÍ±∞
        const $items = $container.find('.performance-info'); // Ïä¨ÎùºÏù¥Îçî ÏïÑÏù¥ÌÖú Ïû¨Ï¥àÍ∏∞Ìôî
        const totalDots = Math.ceil($items.length / itemsToShow);
        for (let i = 0; i < totalDots; i++) {
            const $dot = $('<div>').addClass('dot-rank').data('index', i);
            if (i === 0) $dot.addClass('active');
            $dotsContainer.append($dot);
        }
        // Ï∂îÍ∞Ä dot ÏÉùÏÑ±
        const additionalDots = $items.length - 4 - totalDots;
        for (let i = 0; i < additionalDots; i++) {
            const $extraDot = $('<div>').addClass('dot-rank').data('index', totalDots + i);
            $dotsContainer.append($extraDot);
        }
    };

    const updateDots = () => {
        $('.dot-rank').removeClass('active');
        $('.dot-rank').eq(currentIndex).addClass('active');
    };

    $dotsContainer.off('click', '.dot-rank'); // Í∏∞Ï°¥ Ïù¥Î≤§Ìä∏ Ìï∏Îì§Îü¨ Ï†úÍ±∞
    $dotsContainer.on('click', '.dot-rank', function () {
        const index = $(this).data('index');
        if (index !== undefined) {
            currentIndex = index;
            updateSliderPosition();
        }
    });

    $btnRight.off('click'); // Í∏∞Ï°¥ Ïù¥Î≤§Ìä∏ Ìï∏Îì§Îü¨ Ï†úÍ±∞
    $btnRight.on('click', () => {
        const $items = $container.find('.performance-info'); // Ïä¨ÎùºÏù¥Îçî ÏïÑÏù¥ÌÖú Ïû¨Ï¥àÍ∏∞Ìôî
        if (currentIndex < $items.length - itemsToShow) {
            currentIndex++;
            updateSliderPosition();
        }
    });

    $btnLeft.off('click'); // Í∏∞Ï°¥ Ïù¥Î≤§Ìä∏ Ìï∏Îì§Îü¨ Ï†úÍ±∞
    $btnLeft.on('click', () => {
        if (currentIndex > 0) {
            currentIndex--;
            updateSliderPosition();
        }
    });

    let startX, isDragging = false;

    $container.off('mousedown'); // Í∏∞Ï°¥ Ïù¥Î≤§Ìä∏ Ìï∏Îì§Îü¨ Ï†úÍ±∞
    $container.on('mousedown', (e) => {
        startX = e.pageX;
        isDragging = true;
        e.preventDefault(); // Í∏∞Î≥∏ ÎìúÎûòÍ∑∏ Ïï§ ÎìúÎ°≠ Í∏∞Îä• ÎπÑÌôúÏÑ±Ìôî
    });

    $(document).off('mouseup'); // Í∏∞Ï°¥ Ïù¥Î≤§Ìä∏ Ìï∏Îì§Îü¨ Ï†úÍ±∞
    $(document).on('mouseup', (e) => {
        if (isDragging) {
            isDragging = false;
            // ÎìúÎûòÍ∑∏ Í±∞Î¶¨Í∞Ä 50px Ïù¥ÏÉÅÏùº ÎïåÎßå Ïù¥Îèô
            const endX = e.pageX;
            const diff = startX - endX;
            if (diff > 50 && currentIndex < $container.find('.performance-info').length - itemsToShow) {
                currentIndex++;
            } else if (diff < -50 && currentIndex > 0) {
                currentIndex--;
            }
            updateSliderPosition(); // ÎßàÏö∞Ïä§ Î≤ÑÌäºÏùÑ ÎÜìÏïòÏùÑ Îïå Ïä¨ÎùºÏù¥Îìú ÏÉÅÌÉú ÏóÖÎç∞Ïù¥Ìä∏
            e.preventDefault(); // Í∏∞Î≥∏ ÎìúÎûòÍ∑∏ Ïï§ ÎìúÎ°≠ Í∏∞Îä• ÎπÑÌôúÏÑ±Ìôî
        }
    });

    $container.off('mousemove'); // Í∏∞Ï°¥ Ïù¥Î≤§Ìä∏ Ìï∏Îì§Îü¨ Ï†úÍ±∞
    $container.on('mousemove', (e) => {
        if (isDragging) {
            e.preventDefault(); // Í∏∞Î≥∏ ÎìúÎûòÍ∑∏ Ïï§ ÎìúÎ°≠ Í∏∞Îä• ÎπÑÌôúÏÑ±Ìôî
        }
    });

    // Ï¥àÍ∏∞Ìôî ÏûëÏóÖ
    createDots();
    updateSliderPosition();

    // ÏúàÎèÑÏö∞ Î¶¨ÏÇ¨Ïù¥Ï¶à Ïãú Ïä¨ÎùºÏù¥Îçî Ïû¨Ï¥àÍ∏∞Ìôî
    $(window).off('resize'); // Í∏∞Ï°¥ Ïù¥Î≤§Ìä∏ Ìï∏Îì§Îü¨ Ï†úÍ±∞
    $(window).on('resize', () => {
        updateSliderPosition();
    });
});
